/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable react-hooks/rules-of-hooks */
import {FC, useState, useEffect, useContext} from 'react'
import axios from 'axios'
import { Col, Container, Row, Accordion, Form, Button } from 'react-bootstrap'
import { useCreateVideoRack, transform, checkAuth } from '../hooks'
import './Profile.css'
import { Data, UserData } from '../types'
import { Loading } from '../Loading/Loading'
import { useNavigate } from 'react-router-dom'
import { PlayerContext } from '../../App'

interface Props{

}

export const Profile:FC<Props> = (props) => {

    const {setShowLogin} = useContext(PlayerContext)
    const navigate = useNavigate()

    const [loading, setLoading] = useState(true)
    const [history, setHistory] = useState<Data[]>([])
    const [userData, setUser] = useState<UserData>()

    useEffect(() => {
        if(checkAuth()){
            const config = {
                headers: {
                    'Authorization': `Bearer ${sessionStorage.getItem('accessToken')}`,
                }
            }
            axios.get(`${process.env.PUBLIC_URL}/list/profile`, config)
            .then((response) => {
                if(response.data.history.length !== 0){
                    let temp = transform(response.data.history)
                    setHistory(temp.reverse())
                }
                setUser(response.data.user)
                setLoading(false)
            }).catch((error) => {
                console.error(error)
            })
        }
        else{
            setShowLogin(true)
            navigate('/')
        }
    }, [])

    if(loading){
        return <Loading/>
    }

    return(
        <Container>
            <div id='profileRow'>
                <Col className='mt-4 mb-3'>
                    <img src={`${process.env.PUBLIC_URL}/profilethumb?name=${userData?.channelName}`} alt="profile pic.png" id='profilePic' width={'300px'} height={'300px'} />
                </Col>
                <Col className='mt-4 mb-3'>
                    <p className='profileName'>#{userData?.channelName}</p>
                </Col>
            </div>
            <hr/>
            <Row id='bioRow'>
                <span>About</span>
                <p>{userData?.About}</p>
            </Row>
            <Accordion defaultActiveKey="0" flush alwaysOpen>
                <Accordion.Item eventKey="0">
                    <Accordion.Header>Upload Video</Accordion.Header>
                    <Accordion.Body>
                        <Form action={`${process.env.PUBLIC_URL}/uploadvideo`} method='post' encType='multipart/form-data'>
                            <Row>
                                <Col>
                                    <Form.Group>
                                        <Form.Label>Channel Name</Form.Label>
                                        <Form.Control type='text' name='channel' placeholder='Username' value={userData?.channelName} size='sm' required/>
                                    </Form.Group>
                                </Col>
                                <Col>
                                    <Form.Group>
                                        <Form.Label>Video Name</Form.Label>
                                        <Form.Control type='text' name='videoname' placeholder='Enter Video Name' size='sm' required/>
                                    </Form.Group>
                                </Col>
                            </Row>
                            <Row className='mt-5'>
                                <Col>
                                    <Form.Group>
                                        <Form.Label>Thumbnail</Form.Label>
                                        <Form.Control type='file' name='files' placeholder='Upload video thumbnail' size='sm' required/>
                                    </Form.Group>
                                </Col>
                                <Col>
                                    <Form.Group>
                                        <Form.Label>Video File</Form.Label>
                                        <Form.Control type='file' name='files' placeholder='Upload video file' size='sm' required/>
                                    </Form.Group>
                                </Col>
                            </Row>
                            <Row className='mt-5'>
                                <Form.Group>
                                    <Form.Label>Description</Form.Label>
                                    <Form.Control id='desc' as='textarea' name='description' placeholder='Enter video description here' required/>
                                </Form.Group>
                            </Row>
                            <Row className='mt-5'>
                                <h5>Genres</h5>
                                <Col>
                                    <Form.Group>
                                        <Form.Check type='checkbox' id='Action' name='genres' value={'Action'} label={'Action'}/>
                                        <Form.Check type='checkbox' id='Film Production' name='genres' value={'Film Production'} label={'Film Production'}/>
                                        <Form.Check type='checkbox' id='Motivational' name='genres' value={'Motivational'} label={'Motivational'}/>
                                        <Form.Check type='checkbox' id='Animation' name='genres' value={'Animation'} label={'Animation'}/>
                                        <Form.Check type='checkbox' id='Gaming' name='genres' value={'Gaming'} label={'Gaming'}/>
                                    </Form.Group>
                                </Col>
                                <Col>
                                    <Form.Group>
                                        <Form.Check type='checkbox' id='Comedy' name='genres' value={'Comedy'} label={'Comedy'}/>
                                        <Form.Check type='checkbox' id='Testimonial' name='genres' value={'Testimonial'} label={'Testimonial'}/>
                                        <Form.Check type='checkbox' id='Inspirational' name='genres' value={'Inspirational'} label={'Inspirational'}/>
                                        <Form.Check type='checkbox' id='Experiment' name='genres' value={'Experiment'} label={'Experiment'}/>
                                        <Form.Check type='checkbox' id='Anime' name='genres' value={'Anime'} label={'Anime'}/>
                                    </Form.Group>
                                </Col>
                                <Col>
                                    <Form.Group>
                                        <Form.Check type='checkbox' id='Tutorial' name='genres' value={'Tutorial'} label={'Tutorial'}/>
                                        <Form.Check type='checkbox' id='Reaction' name='genres' value={'Reaction'} label={'Reaction'}/>
                                        <Form.Check type='checkbox' id='Society' name='genres' value={'Society'} label={'Society'}/>
                                        <Form.Check type='checkbox' id='Historical' name='genres' value={'Historical'} label={'Historical'}/>
                                        <Form.Check type='checkbox' id='Design' name='genres' value={'Design'} label={'Design'}/>
                                    </Form.Group>
                                </Col>
                                <Col>
                                    <Form.Group>
                                        <Form.Check type='checkbox' id='School Work' name='genres' value={'School Work'} label={'School Work'}/>
                                        <Form.Check type='checkbox' id='Webinar' name='genres' value={'Webinar'} label={'Webinar'}/>
                                        <Form.Check type='checkbox' id='Culture' name='genres' value={'Culture'} label={'Culture'}/>
                                        <Form.Check type='checkbox' id='Science' name='genres' value={'Science'} label={'Science'}/>
                                        <Form.Check type='checkbox' id='Music' name='genres' value={'Music'} label={'Music'}/>
                                    </Form.Group>
                                </Col>
                                <Col>
                                    <Form.Group>
                                        <Form.Check type='checkbox' id='Programming' name='genres' value={'Programming'} label={'Programming'}/>
                                        <Form.Check type='checkbox' id='Finance' name='genres' value={'Finance'} label={'Finance'}/>
                                        <Form.Check type='checkbox' id='Travel' name='genres' value={'Travel'} label={'Travel'}/>
                                        <Form.Check type='checkbox' id='Skit' name='genres' value={'Skit'} label={'Skit'}/>
                                        <Form.Check type='checkbox' id='News' name='genres' value={'News'} label={'News'}/>
                                    </Form.Group>
                                </Col>
                                <Col>
                                    <Form.Group>
                                        <Form.Check type='checkbox' id='Educational' name='genres' value={'Educational'} label={'Educational'}/>
                                        <Form.Check type='checkbox' id='Business' name='genres' value={'Business'} label={'Business'}/>
                                        <Form.Check type='checkbox' id='Tourism' name='genres' value={'Tourism'} label={'Tourism'}/>
                                        <Form.Check type='checkbox' id='Promotional' name='genres' value={'Promotional'} label={'Promotional'}/>
                                        <Form.Check type='checkbox' id='Shorts' name='genres' value={'Shorts'} label={'Shorts'}/>
                                    </Form.Group>
                                </Col>
                                <Col>
                                    <Form.Group>
                                        <Form.Check type='checkbox' id='Challenge' name='genres' value={'Challenge'} label={'Challenge'}/>
                                        <Form.Check type='checkbox' id='Life Advice' name='genres' value={'Life Advice'} label={'Life Advice'}/>
                                        <Form.Check type='checkbox' id='Vlogs' name='genres' value={'Vlogs'} label={'Vlogs'}/>
                                        <Form.Check type='checkbox' id='Documentaries' name='genres' value={'Documentaries'} label={'Documentaries'}/>
                                        <Form.Check type='checkbox' id='Sports' name='genres' value={'Sports'} label={'Sports'}/>
                                    </Form.Group>
                                </Col>
                                <Col>
                                    <Form.Group>
                                        <Form.Check type='checkbox' id='Unboxing' name='genres' value={'Unboxing'} label={'Unboxing'}/>
                                        <Form.Check type='checkbox' id='Podcast' name='genres' value={'Podcast'} label={'Podcast'}/>
                                        <Form.Check type='checkbox' id='Art' name='genres' value={'Art'} label={'Art'}/>
                                        <Form.Check type='checkbox' id='Parody' name='genres' value={'Parody'} label={'Parody'}/>
                                        <Form.Check type='checkbox' id='Others' name='genres' value={'Others'} label={'Others'}/>
                                    </Form.Group>
                                </Col>
                            </Row>
                            <Form.Control type='hidden' name='token' value={`Bearer ${sessionStorage.getItem('accessToken')}`} size='sm' required/>
                            <Button className='mt-4' type='submit' size='lg'>Submit</Button>
                        </Form>
                    </Accordion.Body>
                </Accordion.Item>
                <Accordion.Item eventKey="1">
                    <Accordion.Header>Update User Info</Accordion.Header>
                    <Accordion.Body>
                        <Form action={`${process.env.PUBLIC_URL}/updateuser`} method='post' encType='multipart/form-data'>
                            <Row>
                                <Col>
                                    <Form.Group>
                                        <Form.Label>Username</Form.Label>
                                        <Form.Control type='text' name='channel' value={userData?.channelName} placeholder='Username' size='sm'/>
                                    </Form.Group>
                                </Col>
                                <Col>
                                    <Form.Group>
                                        <Form.Label>Password</Form.Label>
                                        <Form.Control type='password' name='password' placeholder='Enter new password' size='sm'/>
                                    </Form.Group>
                                </Col>
                            </Row>
                            <Row>
                                <Col>
                                    <Form.Group>
                                        <Form.Label>Update Profile Pic</Form.Label>
                                        <Form.Control type='file' name='file' placeholder='Upload new profile picture' size='sm'/>
                                    </Form.Group>
                                </Col>
                            </Row>
                            <Row>
                                <Form.Group>
                                    <Form.Label>Update About Information</Form.Label>
                                    <Form.Control as='textarea' name='description' placeholder='Enter new channel description here'/>
                                </Form.Group>
                            </Row>
                            <Form.Control type='hidden' name='token' value={`Bearer ${sessionStorage.getItem('accessToken')}`} size='sm' required/>
                            <Button className='mt-4 mb-5' type='submit' size='lg'>Submit</Button>
                        </Form>
                    </Accordion.Body>
                </Accordion.Item>
                <Accordion.Item eventKey="2">
                    <Accordion.Header>History</Accordion.Header>
                    <Accordion.Body>
                        <Row id='historyRow'>
                            {useCreateVideoRack(history)}
                        </Row>
                    </Accordion.Body>
                </Accordion.Item>
            </Accordion>
        </Container>
    )
}