/* eslint-disable react-hooks/rules-of-hooks */
import { FC, useEffect, useState } from "react";
import { Data } from "../types";
import './Home.css'
import {Container, Row, Col} from 'react-bootstrap';
import { transform, useCreateVideoRack } from "../hooks";
import axios from "axios";
import { Loading } from "../Loading/Loading";

export interface Props {
    data?: any,
}

export const Home:FC<Props> = (props) => {

   const [dataList, setDataList] = useState<Data[]>([])
   const [loading, setLoading] = useState(true)

    useEffect(() => {
        axios.get(`${process.env.PUBLIC_URL}/list/home`)
        .then((response) => {
            let temp = transform(response.data)
            setDataList(temp)
            setLoading(false)
        }).catch((error) => {
            console.error(error)
        })
    }, [])

    if(loading){
        return <Loading/>
    }

    return(
        <Container className="mt-3">
            <Row id="homerandomvideorow">
               <div className="topvideo">
                    <h4>Random Video of the day</h4>
                    <br/><br/><hr/>
                    <Row id="anchor">
                        <Col className=" col-12 mb-5 mt-5 homerandomvideocol">
                            <video id="homerandomvideo" width={'100%'} height={'350px'} controls controlsList="nodownload">
                                <source src={`${process.env.PUBLIC_URL}/randomvideo`} type="video/mp4"/>
                            </video>
                            {/* <embed type="video/mp4" src="https://www.youtube.com/embed/QEmreA1H2Io" width="100%" height="350px"></embed> */}
                        </Col>
                        <Col className="offset-1"></Col>
                    </Row>
               </div>
            </Row>
            <div className="genTitle">
                <h4>Popular Videos</h4>
            </div>
            <hr/>
            <Row className="vidrack">
                {useCreateVideoRack(dataList)}
            </Row>
            <Row className="mt-3 mb-3">
                <h6><a  className="base" href="#anchor">Back to the Top <i className="fa fa-arrow-up"></i></a></h6>
            </Row>
        </Container>
    )
}