/* eslint-disable react-hooks/exhaustive-deps */
import axios from 'axios'
import { FC, useEffect, useState } from 'react'
import { Container } from "react-bootstrap"
import { Loading } from '../Loading/Loading'


interface Props {
    token: string
}


export const Validate:FC<Props> = (props) => {

    const [loading, setLoading] = useState(true)
    const [msg, setMsg] = useState('')

    useEffect(() => {
        axios.get(`${process.env.PUBLIC_URL}/auth/validatesignin?token=${props.token}`)
            .then((res) => {
                if(res.data.status === 'ok'){
                    setLoading(false)
                    setMsg('Sign Up successful! You can now login.')
                }
                if(res.data.status === 'error'){
                    setLoading(false)
                    setMsg('An Error occurred. Please try again.')
                }
            })
            .catch((err) => {
                setLoading(false)
                setMsg('An Error occurred. Please try again.')
            })
    },[])

    if(loading){
        return(
            <Container className="signupcontainer mt-5">
                <div className="signupform after">
                    <Loading/>
                </div>
            </Container>
        )
    }

    return(
        <Container className="signupcontainer mt-5">
            <div className="signupform after">
                <h6>{msg}</h6>
            </div>
        </Container>
    )
}