import { PayPalScriptProvider, PayPalButtons, usePayPalScriptReducer } from "@paypal/react-paypal-js"
import { FC, useRef } from "react"
import { Container, Row } from "react-bootstrap"
import styles from './Donate.module.css'


interface Props {

}

export const Donate:FC<Props> = (props) => {

    const key = process.env.REACT_APP_PAYPAL_KEY
    const scrollRef = useRef<HTMLDivElement>(null)

    const handleScroll = () => {
        scrollRef.current?.scrollIntoView({behavior: 'smooth', block: 'start'})
    }
    
    const ButtonWrap = () => {
        const [{isPending}] = usePayPalScriptReducer()

        if(isPending){
            return(
                <Container className={styles.container}>
                    <i id={styles.load} className='fa fa-circle-o-notch fa-3x'></i>
                </Container>
            )
        }

        return(
            <PayPalButtons onClick={() => {setTimeout(() => handleScroll(), 5000)}}/>
        )
    }
    
    return(
        <Container className="align-items-center">
            <Row style={{height: '530px'}}>
                <p className={styles.thanks}>Thank you <br/>for your <br/>Donation</p>
            </Row>
            <Row>
                <PayPalScriptProvider options={{
                    "client-id": key!}}>
                    <ButtonWrap/>
                </PayPalScriptProvider>
            </Row>
            <div ref={scrollRef}></div>
        </Container>
    )
}