import { FC, useContext, useRef, useState } from "react"
import { Col, Container, OverlayTrigger, Row, Tooltip } from "react-bootstrap"
import Popup from 'reactjs-popup'
import 'reactjs-popup/dist/index.css'
import { PlayerContext } from "../../App"
import './Player.css'
import { addToLibrary, checkAuth, intToString, renderGenres, stopPlay } from "../hooks"
import axios from "axios"
import { Link } from "react-router-dom"
import { Helmet } from "react-helmet-async"

interface Props {
    
}

export const Player:FC<Props> = (props) => {

    const playing = useContext(PlayerContext)
    const likeButton = useRef<HTMLElement>(null)
    const dislikeButton = useRef<HTMLElement>(null)
    const [likedColor, setLikedColor] = useState('white')
    const [dislikedColor, setDisLikedColor] = useState('white')


    const handleLike = (videoName: string, channelName: string, setShowLogin: (a: boolean) => void, currentLikes: number, currentDisLikes: number) => {
        if(!checkAuth()){
            setShowLogin(true)
            return
        }
        const config = {
            headers: {
                'Authorization': `Bearer ${sessionStorage.getItem('accessToken')}`,
            }
        }
        axios.post(`${process.env.PUBLIC_URL}/videoInfo/like`, {videoId: `${videoName}_${channelName}`}, config)
        .then((response) => {
            if(response.data.status === 'ok'){
                if(dislikeButton.current?.style.color === 'purple'){
                    dislikeButton.current.style.color = 'white'
                    likeButton!.current!.style.color = 'purple'
                    playing.setVideoInfo({
                        ...playing.videoInfo,
                        dislikes: (currentDisLikes - 1),
                        likes: currentLikes + 1
                    });
                }
                else{
                    likeButton!.current!.style.color = 'purple'
                    playing.setVideoInfo({
                        ...playing.videoInfo,
                        likes: playing.videoInfo.likes + 1
                    });
                }
            }
            if(response.data.status === 'unliked'){
                likeButton!.current!.style.color = 'white'
                playing.setVideoInfo({
                    ...playing.videoInfo,
                    likes: playing.videoInfo.likes - 1
                });
            }
        }).catch((error) => {
            console.error(error)
        })
    }

    const handleDisLike = (videoName: string, channelName: string, setShowLogin: (a: boolean) => void, currentLikes: number, currentDisLikes: number) => {
        if(!checkAuth()){
            setShowLogin(true)
            return
        }
        const config = {
            headers: {
                'Authorization': `Bearer ${sessionStorage.getItem('accessToken')}`,
            }
        }
        axios.post(`${process.env.PUBLIC_URL}/videoInfo/dislike`, {videoId: `${videoName}_${channelName}`}, config)
        .then((response) => {
            if(response.data.status === 'ok'){
                if(likeButton.current?.style.color === 'purple'){
                    likeButton.current.style.color = 'white'
                    dislikeButton!.current!.style.color = 'purple'
                    playing.setVideoInfo({
                        ...playing.videoInfo,
                        likes: (currentLikes - 1),
                        dislikes: currentDisLikes + 1
                    });
                }
                else{
                    dislikeButton!.current!.style.color = 'purple'
                    playing.setVideoInfo({
                        ...playing.videoInfo,
                        dislikes: playing.videoInfo.dislikes + 1
                    });
                }
            }
            if(response.data.status === 'undisliked'){
                dislikeButton!.current!.style.color = 'white'
                playing.setVideoInfo({
                    ...playing.videoInfo,
                    dislikes: playing.videoInfo.dislikes - 1
                });
            }
        }).catch((error) => {
            console.error(error)
        })
    }

    const handleReport = (videoName: string, channelName: string) => {
        axios.post(`${process.env.PUBLIC_URL}/report/video`, {videoId: `${videoName}_${channelName}`})
            .then((res) => {
                if(res.data.status === 'ok'){
                    playing.setNotifyMessage('Report has been sent.')
                    playing.setShowNotify(true)
                    return
                }
                else{
                    playing.setNotifyMessage('An error occurred.')
                    playing.setShowNotify(true)
                    return
                }
            })
    }

    const getColorLiked = () => {
        if(checkAuth()){
            const config = {
                headers: {
                    'Authorization': `Bearer ${sessionStorage.getItem('accessToken')}`,
                }
            }
            axios.get(`${process.env.PUBLIC_URL}/list/liked`, config)
            .then((response) => {
                if(response.data.liked.includes(`${playing.videoName}_${playing.videoInfo.channelName}`)){
                    setLikedColor('purple')
                }
                else{
                    setLikedColor('white')
                }
                if(response.data.disliked.includes(`${playing.videoName}_${playing.videoInfo.channelName}`)){
                    setDisLikedColor('purple')
                }
                else{
                    setDisLikedColor('white')
                }
            }).catch((error) => {
                console.error(error)
            })
        }
    }

    getColorLiked()


    return(
        <Popup open={playing.showPlayer} contentStyle={{padding: 0
        , borderColor: '#282c34', height: '100%', width: '1000px'
        , backgroundColor: '#282c34', position: "relative", top: 0, zIndex: 101, overflowY: 'auto'}} closeOnDocumentClick={false}>
            <Helmet>
                <title>{playing.videoName} | GamaReel</title>
                <link rel="canonical" href={window.location.href} />
                <meta name="description" content="GamaReel. The place to find any video you need. This is the sign up page"/>
                <meta name="keywords" content="video sharing site, GamaReel, watch video for free, find any video, better than youtube, sign up, free streaming"/>
                <meta property="og:locale" content="en_US" />
                <meta property="og:type" content="video" />
            </Helmet>
            <Container className="player">
                <Row className="d-flex flex-row-reverse mb-1">
                    <button className="playerclosebutton" onClick={() => stopPlay(playing.setPlayingVideo, playing.setShowPlayer)}><i className="fa fa-close"></i></button>
                </Row>
                <Row>
                    <video width={'100%'} height={'500px'} controls controlsList="nodownload">
                        <source src={playing.playingVideo}/>
                    </video>
                    {/* <embed type="application/x-vlc-plugin" src={playing.playingVideo} width='800px' height='500px'></embed> */}
                </Row>
                <Row>
                    <hr/>
                    <Col className="col-7 videonamecol">
                        <div className="videoname">{playing.videoName}</div>
                    </Col>
                    <Col id="videoInfo" className="mt-2">
                        <div className="views">{intToString(playing.videoInfo.views)} views</div>
                        <i id="thumbsup" className="fa fa-thumbs-up" ref={likeButton}

                            onClick={() => handleLike(playing.videoName, playing.videoInfo.channelName
                                , playing.setShowLogin, playing.videoInfo.likes, playing.videoInfo.dislikes)}
                             
                            style={{color: likedColor, position: 'relative', top: '0', height: '25%'}}>
                            {intToString(playing.videoInfo.likes)}
                        </i>

                        &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;

                        <i id="thumbsdown" className="fa fa-thumbs-down" ref={dislikeButton}

                            onClick={() => handleDisLike(playing.videoName, playing.videoInfo.channelName
                            , playing.setShowLogin, playing.videoInfo.likes, playing.videoInfo.dislikes)}
                         
                            style={{color: dislikedColor, position: 'relative', top: '0', height: '25%'}}>
                            {intToString(playing.videoInfo.dislikes)}
                        </i>
                    </Col>
                    <Col className="mt-2">
                        <button onClick={() => addToLibrary(playing.videoName, playing.videoInfo.channelName, playing.setShowLogin, playing.setShowNotify, playing.setNotifyMessage)} style={{backgroundColor: '#282c34' , borderStyle: 'none', borderRadius: '50%' 
                        , marginLeft: '40px'}}><i style={{color: 'white'}} className="fa fa-plus"></i></button>
                    </Col>
                    <Col className="mt-2">
                        <OverlayTrigger placement="bottom"
                            overlay={
                                <Tooltip>
                                    report video
                                </Tooltip>
                            }>
                            <i id="thumbsup" className="fa fa-flag" onClick={() => handleReport(playing.videoName, playing.videoInfo.channelName)}></i>
                        </OverlayTrigger>
                    </Col>
                    <hr/>
                </Row>
                <Row className="mb-3">
                    <Col lg={"1"}>
                        <img src={`${process.env.PUBLIC_URL}/profilethumb?name=${playing.videoInfo.channelName}`} height="60px" width="60px" style={{borderRadius: '50%'}} alt="" />
                    </Col>
                    <Col lg={"2"}>
                        <Link to={`/channel?name=${playing.videoInfo.channelName}`} className='channelLink'>
                            <div className="channelname">{playing.videoInfo.channelName}</div>
                        </Link>
                    </Col>
                    <Col className="mt-4">
                        <div className="genres">{renderGenres(playing.videoInfo.genres)}</div>
                    </Col>
                </Row>
                <Row className="description mt-2">
                    <b>Description</b>
                    {playing.videoInfo.description}
                </Row>
            </Container>
        </Popup>
    )
}