import React, { useState, createContext } from 'react';
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Routes } from 'react-router-dom';
import { Route } from 'react-router';
import { HomePage } from './pages/HomePage';
import { RecommendPage } from './pages/RecommendPage';
import { LibraryPage } from './pages/LibraryPage';
import { DonatePage } from './pages/DonatePage';
import { currentVideoInfo, GlobalContent } from './components/GlobalContext';
import { ProfilePage } from './pages/ProfilePage';
import { Login } from './components/Login/Login';
import { ChannelPage } from './pages/ChannelPage';
import { SignupPage } from './pages/SignupPage';
import { ValidationPage } from './pages/ValidationPage';
import { SearchPage } from './pages/SearchPage';
import { OfflineCheck } from './components/OfflineCheck/OfflineCheck';
import { Helmet, HelmetProvider } from 'react-helmet-async';


export const PlayerContext = createContext<GlobalContent>({
  playingVideo: '',
  setPlayingVideo: () => { },
  showPlayer: false,
  setShowPlayer: () => { },
  videoName: '',
  setVideoName: () => {},
  videoInfo: {views: 0, genres: [], channelName: '', description: '', likes: 0, dislikes: 0},
  setVideoInfo: () => {},
  showLogin: false,
  setShowLogin: () => {},
  notifyMessage: '',
  setNotifyMessage: () => {},
  showNotify: false,
  setShowNotify: () => {}
})

function App() {
  const [showLogin, setShowLogin] = useState(false)
  const [notifyMessage, setNotifyMessage] = useState('')
  const [showNotify, setShowNotify] = useState(false)
  const [playingVideo, setPlayingVideo] = useState<string>('')
  const [showPlayer, setShowPlayer] = useState(false)
  const [videoName, setVideoName] = useState('')
  const [videoInfo, setVideoInfo] = useState<currentVideoInfo>({views: 0, genres: [], channelName: ''
                                                                , description: '', likes: 0, dislikes: 0})

  return (
    <HelmetProvider>
      <div className="App">
        <Helmet>
          <title>GamaReel</title>
          <link rel="canonical" href={window.location.href} />
          <meta name="description" content="GamaReel. The place to find any video you need."/>
          <meta name="keywords" content="video sharing site, GamaReel, watch video for free, find any video, better than youtube, free streaming"/>
          <meta property="og:locale" content="en_US" />
          <meta property="og:type" content="video" />
        </Helmet>
        <PlayerContext.Provider value={{playingVideo, setPlayingVideo
          , showPlayer, setShowPlayer
          , videoName, setVideoName
          , videoInfo, setVideoInfo
          , showLogin, setShowLogin
          , notifyMessage, setNotifyMessage
          , showNotify, setShowNotify}}>
          <Login/>
          <OfflineCheck/>
          <Routes>
            <Route path='/' element={<HomePage/>}/>
            <Route path='/home' element={<HomePage/>}/>
            <Route path='/profile' element={<ProfilePage/>}/>
            <Route path='/recommendations' element={<RecommendPage />}/>
            <Route path='/library' element={<LibraryPage/>}/>
            <Route path='/donations' element={<DonatePage/>}/>
            <Route path='/channel' element={<ChannelPage/>}/>
            <Route path='/signup' element={<SignupPage/>}/>
            <Route path='/validatesignin' element={<ValidationPage/>}/>
            <Route path='/search' element={<SearchPage/>}/>
          </Routes>
        </PlayerContext.Provider>
      </div>
    </HelmetProvider>
  );
}

export default App;
