/* eslint-disable react-hooks/exhaustive-deps */
import {useEffect} from 'react'
import axios from 'axios'
import { Data, responseData, tempData } from "./types"
import { VideoCard } from "./VideoCard/VideoCard"
import { currentVideoInfo } from './GlobalContext'


let dummy: tempData[] = []

export const useCreateVideoRack = (vidData:Data[]) => {
    let vidR = []
        for(let i = 0; i < vidData.length; i++){
            vidR.push(
                <VideoCard key={crypto.randomUUID()}
                 title={vidData[i].title} link={vidData[i].link}
                 videoCardInfo={{channelName: vidData[i].videoCardInfo.channelName, views: vidData[i].videoCardInfo.views, genres: vidData[i].videoCardInfo.genres, description: vidData[i].videoCardInfo.description, likes: vidData[i].videoCardInfo.likes, dislikes: vidData[i].videoCardInfo.dislikes}}/>
            )
        }

        return vidR
}

export const useGetVideoData = (choice: string, setLoading: any, videoName?: string) => {
    //TODO: modify function to get video data from backend for either recommendation, home, or library
    //and get video info for player
    let receiver: Data[] = []

    useEffect(() => {
        if(choice === 'batch'){
            axios.get(`${process.env.PUBLIC_URL}/list/home`)
            .then((response) => {
                receiver = transform(response.data)
                setLoading(false)
            }).catch((error) => {
                console.error(error)
            })
        }
    }, [])

    return receiver
}

export const delLibraryData = (title: string, setItem: any) => {
    let temp = dummy.filter((item) => item.title !== title)
    setItem(temp)
}

export const handlePlay = (videoLink: string
    , setPlayingVideo: (a: string) => void
    , showPlayer: boolean
    , setShowPlayer: (a : boolean) => void
    ,vidN: string
    , setVideoName: (a: string) => void
    , videoInfo: currentVideoInfo
    , setVideoInfo: (a: currentVideoInfo) => void) => {
        setPlayingVideo(videoLink)
        setVideoName(vidN)
        if(checkAuth()){
            const config = {
                headers: {
                    'Authorization': `Bearer ${sessionStorage.getItem('accessToken')}`,
                }
            }
            axios.get(`/videoInfo?name=${vidN}&channel=${videoInfo.channelName}`, config)
                .then((response) => {
                    let results: responseData = response.data
                    let currentinfo: currentVideoInfo = {channelName: videoInfo.channelName, description: videoInfo.description
                    , genres: results.Genres, likes: results.likes, dislikes: results.dislikes, views: results.views}
                    setVideoInfo(currentinfo)
                    setShowPlayer(true)
                })
            return
        }
        axios.get(`/videoInfo?name=${vidN}&channel=${videoInfo.channelName}`)
        .then((response) => {
            let results: responseData = response.data
            let currentinfo: currentVideoInfo = {channelName: videoInfo.channelName, description: videoInfo.description
            , genres: results.Genres, likes: results.likes, dislikes: results.dislikes, views: results.views}
            setVideoInfo(currentinfo)
            setShowPlayer(true)
        })
}

export const stopPlay = (setPlayingVideo: (a: string) => void
, setShowPlayer: (a : boolean) => void) => {
    setPlayingVideo('')
    setShowPlayer(false)
}

export const transform = (dataList: responseData[]) => {
    
    let result:Data[] = []

    for(let i = 0; i < dataList.length; i++){
        let temp:Data = {title: dataList[i].videoName, videoCardInfo: {channelName: dataList[i].channelName, views: dataList[i].views, genres: dataList[i].Genres, description: dataList[i].description, likes: dataList[i].likes, dislikes: dataList[i].dislikes}, link: `${process.env.PUBLIC_URL}/watch?vid=${dataList[i].videoId}`}
        result.push(temp)
    }

    return result
}

export const renderGenres = (genres: string[]) => {
    let genreList = ''
    for(let i = 0; i < genres.length; i++){
        genreList += `#${genres[i]}  `
    }
    return genreList
}

export const renderGenresList = (genres: string[]) => {
    let genreList = []
    for(let i = 0; i < genres.length; i++){
        genreList.push(<p>#{genres[i]}</p>)
    }
    return genreList
}

export const checkAuth = (): boolean => {
    const accessToken  = sessionStorage.getItem('accessToken')
    if(!accessToken){
        return false
    }
    return true
}

export const addToLibrary = (videoName: string, channelName: string, setShowLogin: (a: boolean) => void, setShowNotify: (a: boolean) => void, setNotifyMessage: (a: string) => void) => {
    if(!checkAuth()){
        setShowLogin(true)
        return
    }
    const config = {
        headers: {
            'Authorization': `Bearer ${sessionStorage.getItem('accessToken')}`,
        }
    }
    axios.post(`${process.env.PUBLIC_URL}/library/add`, {videoId: `${videoName}_${channelName}`}, config)
    .then((response) => {
        if(response.data.status === 'ok'){
            setNotifyMessage('Video has been added to your Library')
            setShowNotify(true)
            return
        }
        else{
            setNotifyMessage('An error occurred')
            setShowNotify(true)
            return
        }
    }).catch((error) => {
        console.error(error)
    })
}

export const removeFromCollection = (collection: string, videoId: string, setRender: (a: boolean) => void, setShowNotify: (a: boolean) => void, setNotifyMessage: (a: string) => void) => {
    const config = {
        headers: {
            'Authorization': `Bearer ${sessionStorage.getItem('accessToken')}`,
        }
    }
    if(collection === 'profile'){
        axios.delete(`${process.env.PUBLIC_URL}/list/history?videoId=${videoId}`, config)
            .then((res) => {
                if(res.data.status === 'ok'){
                    setRender(false)
                    setNotifyMessage('Video has been removed from history')
                    setShowNotify(true)
                    return
                }
        })
    }
    else if(collection === 'library'){
        axios.delete(`${process.env.PUBLIC_URL}/list/library?videoId=${videoId}`, config)
            .then((res) => {
                if(res.data.status === 'ok'){
                    setRender(false)
                    if(res.data.section === 'saved'){
                        setNotifyMessage('Video has been removed from Library')
                        setShowNotify(true)
                    }
                    if(res.data.section === 'yours'){
                        setNotifyMessage('You have successfully deleted your video')
                        setShowNotify(true)
                    }
                    return
                }
        })
        .catch((err) => {
            setNotifyMessage('An error occurred. Please try again')
            setShowNotify(true)
        })
    }
}

export const intToString = (num: any) => {
    num = num.toString().replace(/[^0-9.]/g, '');
    if (num < 1000) {
        return num;
    }
    let si = [
      {v: 1E3, s: "K"},
      {v: 1E6, s: "M"},
      {v: 1E9, s: "B"},
      {v: 1E12, s: "T"},
      {v: 1E15, s: "P"},
      {v: 1E18, s: "E"}
      ];
    let index;
    for (index = si.length - 1; index > 0; index--) {
        if (num >= si[index].v) {
            break;
        }
    }
    return (num / si[index].v).toFixed(2).replace(/\.0+$|(\.[0-9]*[1-9])0+$/, "$1") + si[index].s;
}