import axios from 'axios'
import { useState } from 'react'
import { Button, Col, Container, Form, Row } from "react-bootstrap"
import { Loading } from '../Loading/Loading'
import './SignUp.css'



export const SignUp = () => {

    const [errorMsg, setError] = useState('')
    const [email, setEmail] = useState('')
    const [channelName, setChannelName] = useState('')
    const [password, setPassword] = useState('')
    const [password2, setPassword2] = useState('')
    const [file, setFile] = useState(null)
    const [about, setAbout] = useState('')

    const [loading, setLoading] = useState(false)
    const [sent, setSent] = useState(false)

    const handleFileChange = (event: any) => {
        setFile(event.target.files[0])
    }

    const formCheck = (email: string, password: string, password2: string) => {
        const mailCheck = /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(email)
        if(!mailCheck){
            setError('You have entered an invalid email address')
            return false
        }
        if(password !== password2){
            setError('Your two passwords do not match. retype them to confirm')
            return false
        }
        return true
    }

    const handleSubmit = (event: any) => {
        event.preventDefault()
        if(formCheck(email, password, password2)){
            const formData = new FormData()
            formData.append('file', file!)
            formData.append('email', email)
            formData.append('channelName', channelName)
            formData.append('password', password)
            formData.append('about', about)
            setLoading(true)

            axios.post(`${process.env.PUBLIC_URL}/auth/signup`, formData)
                .then((res) => {
                    if(res.data.status === 'ok'){
                        setSent(true)
                        setLoading(false)
                        return
                    }
                    if(res.data.status === 'username taken'){
                        setError('This username is already in use.')
                        setLoading(false)
                        return
                    }
                    if(res.data.status === 'email taken'){
                        setError('This email is already in use.')
                        setLoading(false)
                        return
                    }
                    if(res.data.status === 'no such email'){
                        setError('Email is not valid. Please enter valid email')
                        setLoading(false)
                        return
                    }
                    else{
                        setError('An Error occurred. Please try again.')
                        setLoading(false)
                        return
                    }
                })
                .catch((err) => {
                    setError('An Error occurred. Please try again.')
                    return
                })
        }
    }

    if(sent){
        return(
            <Container className="signupcontainer mt-5">
                <div className="signupform after">
                    <i className="fa fa-5x fa-envelope"></i>
                    <p>Check your email for a verification link. Make sure to check your spam!</p>
                </div>
            </Container>
        )
    }

    if(loading){
        return(
            <>
                <Loading/>
            </>
        )
    }
    
    return(
        <Container className="signupcontainer mt-5">
            <Form className="signupform" onSubmit={handleSubmit}>
                <Row>
                    <h2>Sign Up</h2>
                    <br/><hr/>
                </Row>
                <p className='signuperror'>{errorMsg}</p>
                <Row>
                    <Col>
                        <Form.Group>
                            <Form.Label>Email</Form.Label>
                            <Form.Control type="email" name="email" placeholder="Enter your email"
                                value={email} onChange={(e) => setEmail(e.target.value)} size='sm'/>
                        </Form.Group>
                    </Col>
                    <Col>
                        <Form.Group>
                            <Form.Label>Username Name</Form.Label>
                            <Form.Control type="text" name="channelName" placeholder="Enter your username or channel name"
                                value={channelName} onChange={(e) => setChannelName(e.target.value)} size='sm'/>
                        </Form.Group>
                    </Col>
                </Row>
                <Row>
                    <Col>
                        <Form.Group>
                            <Form.Label>Password</Form.Label>
                            <Form.Control type="password" name="password" placeholder="Enter your password"
                                value={password} onChange={(e) => setPassword(e.target.value)} size='sm'/>
                        </Form.Group>
                    </Col>
                    <Col>
                        <Form.Group>
                            <Form.Label>Confirm Password</Form.Label>
                            <Form.Control type="password" name="password2" placeholder="Confirm your password"
                                value={password2} onChange={(e) => setPassword2(e.target.value)} size='sm'/>
                        </Form.Group>
                    </Col>
                </Row>
                <Row>
                    <Form.Group>
                        <Form.Label>Upload Profile picture</Form.Label>
                        <Form.Control type="file" name="file" placeholder="upload profile picture"
                            onChange={handleFileChange} size='sm'/>
                    </Form.Group>
                </Row>
                <Row className="mt-3 mb-5">
                    <Form.Group>
                        <Form.Label>About</Form.Label>
                        <Form.Control id="desc" as="textarea" name="about" placeholder="Write what your channel's all about"
                            value={about} onChange={(e) => setAbout(e.target.value)} size='sm'/>
                    </Form.Group>
                </Row>
                <Button type='submit' size='lg'>Submit</Button>
            </Form>
        </Container>
    )
}