import { Helmet } from "react-helmet-async"
import { Donate } from "../components/Donate/Donate"
import { Navibar } from "../components/Navibar/Navibar"


export const DonatePage = () => {
    return(
        <>
            <Helmet>
                <title>Donate | GamaReel</title>
                <link rel="canonical" href={window.location.href} />
                <meta name="description" content="GamaReel. The place to find any video you need. donate"/>
                <meta name="keywords" content="video sharing site, GamaReel, watch video for free, find any video, better than youtube, donate, free streaming"/>
                <meta property="og:locale" content="en_US" />
                <meta property="og:type" content="video" />
            </Helmet>
            <Navibar/>
            <Donate/>
        </>
    )
}