import { Helmet } from "react-helmet-async"
import { useSearchParams } from "react-router-dom"
import { Navibar } from "../components/Navibar/Navibar"
import { Validate } from "../components/Validate/Validate"


export const ValidationPage = () => {
    const [token] = useSearchParams()
    return(
        <>
            <Helmet>
                <title>Verify Email | GamaReel</title>
                <link rel="canonical" href={window.location.href} />
                <meta name="description" content="GamaReel. The place to find any video you need. Verify your email"/>
                <meta name="keywords" content="video sharing site, GamaReel, watch video for free, find any video, better than youtube, free streaming"/>
                <meta property="og:locale" content="en_US" />
                <meta property="og:type" content="video" />
            </Helmet>
            <Navibar/>
            <Validate token={token.get('token')!}/>
        </>
    )
}