import { Helmet } from "react-helmet-async"
import { Home } from "../components/Home/Home"
import { Navibar } from "../components/Navibar/Navibar"
import { Notify } from "../components/Notify/Notify"
import { Player } from "../components/Player/Player"


export const HomePage = () => {
    return(
        <>
            <Helmet>
                <title>GamaReel</title>
                <link rel="canonical" href={window.location.href} />
                <meta name="description" content="GamaReel. The place to find any video you need."/>
                <meta name="keywords" content="video sharing site, GamaReel, watch video for free, find any video, better than youtube, free streaming"/>
                <meta property="og:locale" content="en_US" />
                <meta property="og:type" content="video" />
            </Helmet>
            <Player/>
            <Navibar/>
            <Home/>
            <Notify/>
        </>
    )
}