/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable react-hooks/rules-of-hooks */
import { FC, useState, useEffect, useContext } from "react";
import { Container, Row, Accordion } from "react-bootstrap";
import { checkAuth, transform, useCreateVideoRack } from "../hooks";
import { Loading } from "../Loading/Loading";
import { Data } from "../types";
import axios from 'axios'
import styles from './Library.module.css'
import { useNavigate } from "react-router-dom";
import { PlayerContext } from "../../App";


interface Props {

}

export const Library:FC<Props> = (props) => {

    const {setShowLogin} = useContext(PlayerContext)
    const navigate = useNavigate()

    const [loading, setLoading] = useState(true)
    const [savedData, setSaved] = useState<Data[]>([])
    const [channelData, setChannelData] = useState<Data[]>([])

    useEffect(() => {
        if(checkAuth()){
            const config = {
                headers: {
                    'Authorization': `Bearer ${sessionStorage.getItem('accessToken')}`,
                }
            }
            axios.get(`${process.env.PUBLIC_URL}/list/library`, config)
            .then((response) => {
                let temp = []
                if(response.data.saved.length !== 0){
                    temp = transform(response.data.saved)
                    setSaved(temp.reverse())
                }
                if(response.data.channel.length !== 0){
                    temp = transform(response.data.channel)
                    setChannelData(temp.reverse())
                }
                setLoading(false)
            }).catch((error) => {
                console.error(error)
            })
        }
        else{
            setShowLogin(true)
            navigate('/')
        }
    }, [])

    if(loading){
        return <Loading/>
    }

    return(
        <Container id="anchor" className={styles.container}>
            <Row>
                <Accordion defaultActiveKey='0' alwaysOpen>
                    <Accordion.Item eventKey="0">
                        <Accordion.Header><h3>Saved Videos</h3></Accordion.Header>
                            <Accordion.Body>
                                <Row id={styles.savedRow}>
                                    {savedData!.length !== 0 ? useCreateVideoRack(savedData!) : 'You have no saved videos'}
                                </Row>
                            </Accordion.Body>
                    </Accordion.Item>
                    <Accordion.Item eventKey="1">
                        <Accordion.Header><h3>Your Videos</h3></Accordion.Header>
                            <Accordion.Body>
                                <Row id={styles.yourvidRow}>
                                    {channelData!.length !== 0 ? useCreateVideoRack(channelData!) : 'You have not created any videos'}
                                </Row>
                            </Accordion.Body>
                    </Accordion.Item>
                </Accordion>
            </Row>
            <Row className="mt-3 mb-3">
                <h6><a  className="base" href="#anchor">Back to the Top <i className="fa fa-arrow-up"></i></a></h6>
            </Row>
        </Container>
    )
}
