/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable react-hooks/rules-of-hooks */
import axios from 'axios'
import { FC, useEffect, useState } from 'react'
import { Container, Row } from "react-bootstrap"
import { transform, useCreateVideoRack } from '../hooks'
import { Loading } from '../Loading/Loading'
import { Data } from '../types'
import './Search.css'

interface Props {
    searchValue: string
}

export const Search:FC<Props> = (props) => {

    const [loading, setLoading] = useState(true)
    const [found, setFound] = useState(true)
    const [dataList, setDataList] = useState<Data[]>([])

    useEffect(() => {
        axios.post(`${process.env.PUBLIC_URL}/search`, {search: props.searchValue})
            .then((res) => {
                if(res.data.status === 'none'){
                    setFound(false)
                    setLoading(false)
                    return
                }
                if(res.data.found.length !== 0){
                    let temp = transform(res.data.found)
                    setDataList(temp)
                    setLoading(false)
                }
                else{
                    return
                }
            })
    },[])

    if(loading){
        return(
            <Loading/>
        )
    }

    if(!found){
        return(
            <Container>
                <p>No video found.</p>
            </Container>
        )
    }

    return(
        <Container className='searchRack'>
            <Row className="vidRack">
                {useCreateVideoRack(dataList)}
            </Row>
        </Container>
    )
}