import axios from 'axios'
import { useContext, useState } from 'react'
import { Button, Form } from "react-bootstrap"
import Popup from "reactjs-popup"
import { PlayerContext } from '../../App'
import './Login.css'


export const Login = () => {

    const {showLogin, setShowLogin} = useContext(PlayerContext)

    const [username, setUsername] = useState('')
    const [password, setPassword] = useState('')
    const [loadLogin, setLoadLogin] = useState(false)
    const [errorMsg, setError] = useState('')

    const handleSubmit = (event: any) => {
        event.preventDefault()
        setLoadLogin(true)
        const config = {
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded',
            }
        }
        axios.post(`${process.env.PUBLIC_URL}/auth/login`, {username: username, password: password}, config)
            .then((res) => {
                if(res.data.accessToken){
                    setLoadLogin(false)
                    sessionStorage.setItem('accessToken', res.data.accessToken)
                    sessionStorage.setItem('refreshToken', res.data.refreshToken)
                    setShowLogin(false)
                    setError('')
                    setUsername('')
                    setPassword('')
                }
                else{
                    if(res.data.status === 'wrong username or password'){
                        setLoadLogin(false)
                        setError('Wrong Username or Password')
                    }
                    else if(res.data.status === 'inactive'){
                        setLoadLogin(false)
                        setError('your account has not been activated yet.')
                    }
                    else{
                        setLoadLogin(false)
                        setError('Network Error. Please try again')
                    }
                }
            })
            .catch((err) => {
                setLoadLogin(false)
                setError('Network Error. Please try again')
                console.error(err)
            })
    }

    return(
        <Popup open={showLogin} contentStyle={{padding: 0, borderRadius: '20px'
            , borderColor: '#282c34', height: '400px', width: '400px'
            , backgroundColor: '#282c34', position: "relative", top: 0
            , zIndex: 101, overflowY: 'auto', textAlign: 'center', overflow: 'hidden'}} closeOnDocumentClick={false}>

                <div className="loginHead">
                    <h3 className="loginTitle mt-3">You are not Logged In!</h3>
                    <button className="loginclosebutton" onClick={() => setShowLogin(false)}><i className="fa fa-close"></i></button>
                </div>
                <hr/>
                <Form onSubmit={handleSubmit}>
                    <p className='loginError'>{errorMsg}</p>
                    <Form.Group>
                        <Form.Label className="label">Username or Email</Form.Label>
                        <Form.Control className="loginInput" type="text" name="username" placeholder="Enter Username or Email" value={username} onChange={(e) => setUsername(e.target.value)}/>
                    </Form.Group>
                    <Form.Group>
                        <Form.Label className="label">Password</Form.Label>
                        <Form.Control className="loginInput" type="password" name="password" placeholder="Enter Password" value={password} onChange={(e) => setPassword(e.target.value)}/>
                    </Form.Group>
                    <Button className="loginButton mt-3" type='submit'>{loadLogin ? <i id='loginloader' className='fa fa-circle-o-notch'></i> : 'Login'}</Button>
                    <p>or</p>
                    <Button className="loginButton" href='/signup'>SignUp</Button>
                    <p className='loginFooter' onClick={() => setShowLogin(false)}>continue without logging in or siging up</p>
                </Form>
        </Popup>
    )
}