/* eslint-disable react-hooks/exhaustive-deps */
import axios from 'axios'
import { FC, useEffect, useState } from 'react'
import { Container, Col, Row, Form } from 'react-bootstrap'
import { transform, useCreateVideoRack } from '../hooks'
import { Data } from '../types'
import style from './ChannelVideos.module.css'


interface Props {
    channelName: string
}

export const ChannelVideos:FC<Props> = (props) => {

    const [about, setAbout] = useState('')
    const [dataList, setDataList] = useState<Data[]>([])
    const [permanentData, setPermanentData] = useState<Data[]>([])
    const [searchText, setSearchtext] = useState("")

    const handleClose = () => {
        setDataList(permanentData)
        setSearchtext('')
    }

    const handleChange = (e:any) => {
        setSearchtext(e.target.value)
    }

    const handleSearch = (e: any) => {
        e.preventDefault()
        let temp = permanentData.filter((item) => item.title.toLowerCase().includes(searchText.toLowerCase()))
        setDataList(temp)
    }

    useEffect(() => {
        axios.get(`${process.env.PUBLIC_URL}/list/channel?name=${props.channelName}`)
            .then((response) => {
                if(response.data.status === 'success'){
                    setAbout(response.data.about)
                    let temp = transform(response.data.result)
                    setDataList(temp)
                    setPermanentData(temp)
                }
            })
            .catch((err) => {
                console.error(err)
            })
    },[])

    return(
        <Container>
            <div id='profileRow'>
                <Col className='mt-4 mb-3'>
                    <img src={`${process.env.PUBLIC_URL}/profilethumb?name=${props.channelName}`} alt="profile pic.png" id='profilePic' width={'300px'} height={'300px'} />
                </Col>
                <Col className='mt-4 mb-3'>
                    <p className='profileName'>{props.channelName}</p>
                </Col>
            </div>
            <hr/>
            <Row id='bioRow'>
                <Col sm={'12'} lg={'6'}>
                    <span>About</span>
                    <p>{about}</p>
                </Col>
                <Col  sm={'12'} lg={'6'}>
                    <Form id={style.search} onSubmit={handleSearch}>
                        <Row>
                            <Col>
                                <input autoFocus placeholder="  Search for videos" className={style.searchInput} value={searchText} onChange={handleChange}/>
                            </Col>
                            <Col className="col-1">
                                <Row>
                                    <Col>
                                        <button className={style.searchButton} onClick={handleSearch}><i className="fa fa-search"></i></button>
                                    </Col>
                                </Row>
                                <i id={style.searchClose} className="fa fa-close" onClick={handleClose}></i>
                            </Col>
                        </Row>
                    </Form>
                </Col>
            </Row>
            <Row className="vidrack">
                {useCreateVideoRack(dataList!)}
            </Row>
        </Container>
    )
}