import React from 'react';
import ReactDOM from 'react-dom/client';
import {BrowserRouter} from 'react-router-dom'
import './index.css';
import App from './App';
import axios, { AxiosError, AxiosResponse } from 'axios';


const maxRetries = 10
const retryDelay = 3000

axios.interceptors.response.use((response: AxiosResponse): AxiosResponse => {
  return response
}, (err: AxiosError) => {
  const  originalRequest: any = err.config!
  if(originalRequest.url.includes('/onlinecheck')){
    return Promise.reject(err)
  }
  if(err.response?.status === 401 && originalRequest?.url?.includes('/refreshLogin')){
    return Promise.reject(err)
  }

  if(err.response?.status === 403 && !(originalRequest?.url?.includes('/refreshLogin'))){
    sessionStorage.removeItem('accessToken')
    sessionStorage.removeItem('refreshToken')
    window.location.reload()
  }

  else{
    let retryCount = 0
    if(retryCount < maxRetries){
      retryCount += 1
      return new Promise(resolve => setTimeout(() => resolve(axios(originalRequest)), retryDelay))
    }

  }

  return Promise.reject(err)
})

const root = ReactDOM.createRoot(
  document.getElementById('root') as HTMLElement
);
root.render(
  <React.StrictMode>
    <BrowserRouter>
      <App />
    </BrowserRouter>
  </React.StrictMode>
);

