/* eslint-disable no-restricted-globals */
import React, { FC, useState } from "react";
import { Container, Form, Row, Col } from "react-bootstrap";
import { useNavigate } from "react-router-dom";
import style from './search.module.css'

interface Props {
    show: boolean,
    setShow: (a: boolean) => void
}

export const Search:FC<Props> = (props) => {

    const setShow = props.setShow

    const navigate = useNavigate()
    const [showButton, setShowbutton] = useState(false)
    const [searchText, setSearchtext] = useState("")

    const handleClose = () => {
        setShow(false)
        setSearchtext('')
    }

    const handleChange = (e:any) => {
        if(searchText==="" && e.target.value !== ""){
            setShowbutton(true)
        }
        if(searchText !== "" && e.target.value === ""){
            setShowbutton(false)
        }
        setSearchtext(e.target.value)
    }

    const handleSearch = (e: any) => {
        // handle search function
        e.preventDefault()
        navigate(`/search?s=${searchText}`)
        if(location.pathname === '/search'){
            window.location.reload()
            return
        }
        else{
            return
        }
    }

    return(
        <Container className={style.all}>
            <Form id={style.search} className="mb-3" onSubmit={handleSearch}>
                <Row>
                    <Col>
                        <input autoFocus placeholder="  Search for videos" className={style.searchInput} value={searchText} onChange={handleChange}/>
                    </Col>
                    <Col className="col-1">
                        {showButton && <Row>
                                    <Col>
                                        <button className={style.searchButton} onClick={handleSearch}><i className="fa fa-search"></i></button>
                                    </Col>
                                </Row>}
                        <i id={style.searchClose} className="fa fa-close" onClick={handleClose}></i>
                    </Col>
                </Row>
            </Form>
        </Container>
    )
}