/* eslint-disable react-hooks/exhaustive-deps */
import { FC, useContext, useEffect } from "react";
import { PlayerContext } from "../../App";
import './Notify.css'


interface Props {

}


export const Notify:FC<Props> = (props) => {

    const {showNotify, setShowNotify, notifyMessage} = useContext(PlayerContext)

    useEffect(() => {
        if(showNotify){
            setTimeout(() => setShowNotify(false), 5000)
        }
    },[showNotify])
    
    return(
        <>
            {showNotify && <div className="notifybox">
                <div id="notifyMessage">
                    <p id="messageText">{notifyMessage}</p>
                    <i className="fa fa-close cancelNotify"  onClick={() => setShowNotify(false)}></i>
                </div>
        </div>}
        </>
    )
}