import { Helmet } from "react-helmet-async"
import { Navibar } from "../components/Navibar/Navibar"
import { Notify } from "../components/Notify/Notify"
import { Player } from "../components/Player/Player"
import { Profile } from "../components/Profile/Profile"



export const ProfilePage = () => {
    return(
        <>
            <Helmet>
                <title>Profile | GamaReel</title>
                <link rel="canonical" href={window.location.href} />
                <meta name="description" content="GamaReel. The place to find any video you need. This is your profile page"/>
                <meta name="keywords" content="video sharing site, GamaReel, watch video for free, find any video, better than youtube, profile, free streaming"/>
                <meta property="og:locale" content="en_US" />
                <meta property="og:type" content="video" />
            </Helmet>
            <Navibar/>
            <Player/>
            <Profile/>
            <Notify/>
        </>
    )
}