import { Helmet } from "react-helmet-async"
import { Library } from "../components/Library/Library"
import { Navibar } from "../components/Navibar/Navibar"
import { Notify } from "../components/Notify/Notify"
import { Player } from "../components/Player/Player"


export const LibraryPage = () => {
    return(
        <>
            <Helmet>
                <title>Library | GamaReel</title>
                <link rel="canonical" href={window.location.href} />
                <meta name="description" content="GamaReel. The place to find any video you need. This is your library"/>
                <meta name="keywords" content="video sharing site, GamaReel, watch video for free, find any video, better than youtube, library, free streaming"/>
                <meta property="og:locale" content="en_US" />
                <meta property="og:type" content="video" />
            </Helmet>
            <Navibar/>
            <Player/>
            <Library/>
            <Notify/>
        </>
    )
}