import { Helmet } from "react-helmet-async"
import { Navibar } from "../components/Navibar/Navibar"
import { Notify } from "../components/Notify/Notify"
import { Player } from "../components/Player/Player"
import { Recommend } from "../components/Recommend/Recommend"


export const RecommendPage = () => {
    return(
        <>
            <Helmet>
                <title>Recommendations | GamaReel</title>
                <link rel="canonical" href={window.location.href} />
                <meta name="description" content="GamaReel. The place to find any video you need. These are video recommended for you."/>
                <meta name="keywords" content="video sharing site, GamaReel, watch video for free, find any video, better than youtube, better recommendations, free streaming"/>
                <meta property="og:locale" content="en_US" />
                <meta property="og:type" content="video" /> 
            </Helmet>
            <Player/>
            <Navibar/>
            <Recommend/>
            <Notify/>
        </>
    )
}