import { FC, useContext, useState } from "react";
import {Card, Row, Col, OverlayTrigger, Tooltip} from 'react-bootstrap';
import { Link, useLocation } from "react-router-dom";
import { PlayerContext } from "../../App";
import { addToLibrary, handlePlay, removeFromCollection, renderGenresList } from "../hooks";
import { Data } from "../types";
import './VideoCard.css'



export const VideoCard:FC<Data> = (props) => {

    const title = props.title
    const link = props.link
    const Info = props.videoCardInfo

    const {setPlayingVideo, showPlayer, setShowPlayer, setVideoName, setVideoInfo, setShowLogin, setShowNotify, setNotifyMessage} = useContext(PlayerContext)

    const location = useLocation()
    const [show, setShow] = useState(true)

    const imgRender = (imgName: string) => {
        return(
            <img style={{cursor: 'pointer'}} src={`${process.env.PUBLIC_URL}/thumb?name=${imgName}`} width="100%" height="250px" alt="" 
                onClick={() => handlePlay(link!, setPlayingVideo, showPlayer, setShowPlayer, title, setVideoName, Info, setVideoInfo)}/>
        )
    }

    if(location.pathname === '/library' || location.pathname === '/profile'){
        return(
            <>
                {show && <OverlayTrigger
                    placement={"right"}
                    overlay={
                        <Tooltip style={{position:"fixed"}}>
                            {renderGenresList(Info.genres)}
                        </Tooltip>
                    }
                    >
                    <Card
                        bg={'dark'}
                        text={'light'}
                        style={{ width: '300px', borderColor: 'black', marginLeft: 'auto', marginRight: 'auto', height: '400px', display: "flex", flexDirection: 'column' }}
                        className="mb-2 mt-5"
                        
                        >
                        {imgRender(title)}
                        <p style={{cursor: 'pointer'}}  onClick={() => handlePlay(link!, setPlayingVideo, showPlayer, setShowPlayer, title, setVideoName, Info, setVideoInfo)}>{title}</p>
                        <Row style={{position: 'absolute', bottom: '0px', width: '100%'}}>
                            <Col className="mb-3 offset-1">
                                <img src={`${process.env.PUBLIC_URL}/profilethumb?name=${Info.channelName}`} height="40px" width="40px" style={{borderRadius: '50%'}} alt="" />
                            </Col>
                            <Col className="videocardchannel">
                                <Link to={`/channel?name=${Info.channelName}`} className='channelLink'><p>{Info.channelName}</p></Link>
                            </Col>
                            <Col>
                                <button 
                                    onClick={() => removeFromCollection((location.pathname === '/profile' ? 'profile' : 'library'), `${title}_${Info.channelName}`, setShow, setShowNotify, setNotifyMessage)} style={{backgroundColor: '#282c34' , borderStyle: 'none', borderRadius: '50%'}}>
                                    <i style={{color: 'white'}} className="fa fa-close"></i>
                                </button>
                            </Col>
                        </Row>
                    </Card>
                </OverlayTrigger>}
            </>
        )
    }

    return(
        <>
            <OverlayTrigger
                placement={"right"}
                overlay={
                    <Tooltip style={{position:"fixed"}}>
                        {renderGenresList(Info.genres)}
                    </Tooltip>
                }
                >
                <Card
                    bg={'dark'}
                    text={'light'}
                    style={{ width: '300px', borderColor: 'black', marginLeft: 'auto', marginRight: 'auto', height: '400px', display: "flex", flexDirection: 'column' }}
                    className="aight mb-2 mt-5"
                    
                    >
                    {imgRender(title)}
                    <p className="videoCardTitle"  onClick={() => handlePlay(link!, setPlayingVideo, showPlayer, setShowPlayer, title, setVideoName, Info, setVideoInfo)}>{title}</p>
                    <Row style={{position: 'absolute', bottom: '0px', width: '100%'}}>
                        <Col className="mb-3">
                            <img src={`${process.env.PUBLIC_URL}/profilethumb?name=${Info.channelName}`} height="40px" width="40px" style={{borderRadius: '50%'}} alt="" />
                        </Col>
                        <Col className="videocardchannel">
                            <Link to={`/channel?name=${Info.channelName}`} className='channelLink'><p>{Info.channelName}</p></Link>
                        </Col>
                        <Col>
                            <button onClick={() => addToLibrary(props.title, props.videoCardInfo.channelName, setShowLogin, setShowNotify, setNotifyMessage)} style={{backgroundColor: '#282c34' , borderStyle: 'none', borderRadius: '50%'}}>
                                <i style={{color: 'white'}} className="fa fa-plus"></i>
                            </button>
                        </Col>
                    </Row>
                </Card>
            </OverlayTrigger>
        </>
    )
}