import axios from "axios";
import { FC, useContext, useState } from "react";
import { Nav, Navbar, Container } from "react-bootstrap";
import { useNavigate } from "react-router-dom";
import { PlayerContext } from "../../App";
import { checkAuth } from "../hooks";
import { Search } from "../Searchbar/search";
import './Navbar.css'



interface Props {
    data?: any,
}

export const Navibar:FC<Props> = (props) => {

    const [showSearch, setShowSearch] = useState(false)
    const {setShowLogin} = useContext(PlayerContext)

    const showSearchbar = () => setShowSearch(true)
    const navigate = useNavigate()

    const handleLogout = () => {
        if(checkAuth()){
            const config = {
                headers: {
                    'Authorization': `Bearer ${sessionStorage.getItem('accessToken')}`,
                }
            }
            axios.delete(`${process.env.PUBLIC_URL}/auth/logout`, config)
                .then((res) => {
                    sessionStorage.removeItem('accessToken')
                    sessionStorage.removeItem('refreshToken')
                    setShowLogin(true)
                    navigate('/')
                    return
                })
                .catch((err) => {

                })
        }
        else{
            return
        }
    }


    return (
        <Navbar collapseOnSelect expand="lg" variant="dark" bg="dark" sticky="top" style={{zIndex: 800}}>
            <Container fluid>
                <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                <Navbar.Collapse id="responsive-navbar-nav">
                    <Navbar.Brand className="d-flex align-items-center">
                        <img src="/GamaReelNav.jpg" height={'100%'} alt="Icon" />
                        GamaReel
                    </Navbar.Brand>
                    <Nav className="me-auto">
                        <Nav.Link href="home"><i className="fa fa-home"></i>Home</Nav.Link>
                        <Nav.Link href="recommendations"><i className="fa fa-trophy"></i>Recommendations</Nav.Link>
                    </Nav>
                    <Nav>
                        <Nav.Link href="library" style={{borderBottomColor: 'black'}}><i className="fa fa-book"></i>Library</Nav.Link>
                        <Nav.Link href="profile" style={{borderBottomColor: 'black'}}><i className="fa fa-user"></i>Profile</Nav.Link>
                        <Nav.Link href="donations" style={{borderBottomColor: 'black'}}><i className="fa fa-dollar"></i>Donate</Nav.Link>
                        {checkAuth() ? 
                            <Nav.Link style={{borderBottomColor: 'black'}} onClick={handleLogout}><i className="fa fa-sign-out"></i>LogOut</Nav.Link>
                        :   <Nav.Link style={{borderBottomColor: 'black'}} onClick={() => setShowLogin(true)}><i className="fa fa-sign-in"></i>LogIn</Nav.Link>}
                        <Nav.Link style={{borderBottomColor: 'black'}}  onClick={showSearchbar}><i className="fa fa-search"></i></Nav.Link>
                    </Nav>
                    {showSearch && <Search show={showSearch} setShow={setShowSearch}/>}
                </Navbar.Collapse>
            </Container>
        </Navbar>
    )
}
