import axios from 'axios'
import { useEffect, useState } from 'react'
import { Row } from "react-bootstrap"
import './OfflineCheck.css'


export const OfflineCheck = () => {

    const [online, setOnline] = useState(true)

    useEffect(() => {
        setInterval(() => check(), 5000)
    },[])

    const check = () => {
        axios.get(`${process.env.PUBLIC_URL}/onlinecheck`)
            .then((res) => {
                if(res.data.status === 'ok'){
                    setOnline(true)
                }
                else{
                    setOnline(false)
                }
            })
            .catch((err) => {
                setOnline(false)
            })
    }

    if(online){
        return(
            <></>
        )
    }

    return(
        <Row className="offlinecheckbody">
            <p><i id="offlineicon" className="fa fa-wifi"></i> &nbsp;You are offline.</p>
        </Row>
    )
}