import { Helmet } from "react-helmet-async"
import { useSearchParams } from "react-router-dom"
import { Navibar } from "../components/Navibar/Navibar"
import { Notify } from "../components/Notify/Notify"
import { Player } from "../components/Player/Player"
import { Search } from "../components/Search/Search"


export const SearchPage = () => {

    const [id] = useSearchParams()

    return(
        <>
            <Helmet>
                <title>Search | {id.get('s')}</title>
                <link rel="canonical" href={window.location.href} />
                <meta name="description" content="GamaReel. The place to find any video you need. Search for videos"/>
                <meta name="keywords" content="video sharing site, GamaReel, watch video for free, find any video, better than youtube, find any video, free streaming"/>
                <meta property="og:locale" content="en_US" />
                <meta property="og:type" content="video" />
            </Helmet>
            <Navibar/>
            <Player/>
            <Search searchValue={id.get('s')!}/>
            <Notify/>
        </>
    )
}