import { FC, useState, useEffect } from "react";
import { Container, Row } from "react-bootstrap";
import { useCreateVideoRack, transform } from "../hooks";
import { Loading } from "../Loading/Loading";
import { Data } from "../types";
import axios from 'axios'
import style from './Recommend.module.css'


interface Props {

}

export const Recommend:FC<Props> = (props) => {

    const [recommendList, setRecommendList] = useState<Data[]>([])

    const [loading, setLoading] = useState(true)
 
     useEffect(() => {
         handleGet()
     }, [])

    const result = useCreateVideoRack(recommendList)

    const handleGet = (e?: any) => {
        if(e){
            e.preventDefault()
        }
        setLoading(true)
        axios.get(`${process.env.PUBLIC_URL}/list/recommend`)
         .then((response) => {
             let temp = transform(response.data)
             setRecommendList(temp)
             setLoading(false)
         }).catch((error) => {
             console.error(error)
         })
    }
                                                
    if (loading){
        return <Loading/>
    }

    return(
       <Container className={style.container}>
            <Row id={style.row}>
                <h6 id="anchor"><a  className={style.base} href="#random" onClick={handleGet}>Refresh <i id={style.load} className="fa fa-refresh"></i></a></h6>
            </Row>
            <Row className="mt-3 mb-3 vidrack">
                {result}
            </Row>
            <Row className="mt-3 mb-3">
                <h6><a  className="base" href="#anchor">Back to the Top <i className="fa fa-arrow-up"></i></a></h6>
            </Row>
       </Container>
    )
}