import { Helmet } from "react-helmet-async"
import { useSearchParams } from "react-router-dom"
import { ChannelVideos } from "../components/ChannelVideos/ChannelVideos"
import { Navibar } from "../components/Navibar/Navibar"
import { Notify } from "../components/Notify/Notify"
import { Player } from "../components/Player/Player"



export const ChannelPage = () => {
    
    const [id] = useSearchParams()

    return(
        <>
            <Helmet>
                <title>{id.get('name')} | GamaReel</title>
                <link rel="canonical" href={window.location.href} />
                <meta name="description" content="GamaReel. The place to find any video you need. This is the sign up page"/>
                <meta name="keywords" content="video sharing site, GamaReel, watch video for free, find any video, better than youtube, sign up, free streaming"/>
                <meta property="og:locale" content="en_US" />
                <meta property="og:type" content="video" />
            </Helmet>
            <Navibar/>
            <Player/>
            <ChannelVideos channelName={id.get('name')!}/>
            <Notify/>
        </>
    )
}